* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

p {
  margin: 0;
}

.non-displayed {
  display: none;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.link {
  text-decoration: none;
  transition: 0.3s;
}
.link:visited {
  color: currentColor;
}
.link:focus {
  color: currentColor;
}
.link:hover {
  color: currentColor;
}
.link:active {
  color: currentColor;
}

.btn {
  border: none;
  cursor: pointer;
  transition: 0.3s;
}
.btn-search {
  position: relative;
  width: 50px;
  height: 40px;
  border-radius: 0 10px 10px 0;
  background-color: #754040;
}
.btn-search:hover {
  background-color: #847173;
}
.btn-search::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 10px;
  width: 30px;
  height: 30px;
  background: url("../assets/images/icons/css/svg/sprite.css-c637028f.svg") 69.2307692308% 0 no-repeat;
}